import React, { Component } from 'react';
import MyList from './myList';
import AddTodo from './addTodos';
import stateLists from './listJson.json'
class App extends Component {
  constructor(props){
    super(props);
    this.state=this.state
  }
  state={
    stateLists,
    schedules:[{'description':'my description','deadline':'my deadline'},
                  {'description':'my description2','deadline':'my deadline2'},
                  {'description':'my description3','deadline':'my deadline3'}
                ],
  description:'',
  deadline:'',
  errors:'',
  isEditingActive:false,
  textDecorationLine:false,
  color:'',
  active:''
  }
onChange=(index,event)=>{
  let myCopyOfData=this.state.stateLists;

  if(event.target.checked===true){
    myCopyOfData[index].done=true;
    myCopyOfData[index].description="----"+myCopyOfData[index].description+"----"+myCopyOfData[index].deadline+"----"

    this.setState(myCopyOfData)
  }else{
    myCopyOfData[index].done=false
    myCopyOfData[index].description=myCopyOfData[index].description+myCopyOfData[index].deadline
    this.setState(myCopyOfData)

  }
}
onUpdate=(index)=>{
  alert(index+" update")
}
onDelete=(index)=>{
  let myCopyOfData=this.state.schedules;
  myCopyOfData.splice(index, 1);
  alert("deleted")
  this.setState({myCopyOfData})
}
onEdit=(index)=>{
//  let myCopyOfData=this.state
   this.setState({
     description:this.state.schedules[index].description,
     deadline:this.state.schedules[index].deadline
   })
}
handlesubmit=()=>{
const cp=this.state
console.log(cp)
  if(this.state.description==='' || this.state.deadline===''){
     return alert("Enter description and Deadline")
  }
  const itemToAdd={
    'description':this.state.description,
    'deadline':this.state.deadline
  }

  cp.schedules.push(itemToAdd);
  cp.description='';
  cp.deadline='';
  this.setState({
    schedules:[...this.state.schedules, itemToAdd]
   });
}
handleDeadlineChange=(value)=>{
  this.setState({
    deadline:value
  })
}
handleDescriptionChange=(value)=>{
  this.setState({
    description:value
  })
}
handleDeadlineUpdate=(index,value)=>{
  let myCopyOfData=this.state.schedules;
  this.setState({
    deadline:myCopyOfData[index].deadline
  })
}
handleDescriptionUpdate=(index,value)=>{
  let myCopyOfData=this.state.schedules;
  this.setState({
    description:myCopyOfData[index].description
  })

}

render() {
  //let myClass=this.state.textDecorationLine===false ? 'color:blue':'color:red'
    return (

      <div className="App App-intro text-center">
        <header className="App-header text-center">
        <h1>to do list</h1>
        </header>
          <h4>home work2</h4>
              {this.state.stateLists.map((list,index)=>
               <MyList key={index} description={list.description} deadline={list.deadline} onChange={(event)=>
                 this.onChange(index,event)}>{list.description +"  "+list.deadline+ " "+this.state.textDecorationLine}
                </MyList>
              )}
          <h4>home work3</h4>
              {this.state.schedules.map((list,index)=>
                <MyList key={index} description={list.description}
                deadline={list.deadline}
                ondelete={()=>this.onDelete(index)}
                onedit={()=>this.onEdit(index)}
                onupdate={()=>this.onUpdate(index)}
                handleDescriptionUpdate={()=>this.handleDescriptionUpdate(index)}
                handleDeadlineUpdate={()=>this.handleDeadlineUpdate(index)}
                style={{background:this.state.color}}>
                {list.description +"  "+list.deadline}
                </MyList>
              )}
            <AddTodo handleDescriptionChange={this.handleDescriptionChange}
              handleDeadlineChange={this.handleDeadlineChange}
              handlesubmit={this.handlesubmit}
              />
      </div>
    );
  }
}
export default App;



